.container{
  align-items: flex-end;
  bottom: 5vh;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  gap: 5%;
  height: 70vh;
  background-color: rgba(0, 0, 0, 0.4);
  transition: visibility 0.7s, left 0.7s ease ;
}

.items{
    position: absolute;
    top: 15vh;
    height: 85vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3%;
    width: 100%;
    overflow: scroll;
}

.store{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
}
